type User = {
  first_name: string;
  last_name?: string;
  email?: string;
};

function userLabel(user: User, defaultValue: string = "") {
  const { first_name, last_name, email } = user || {};
  if (!defaultValue?.length && email?.length) defaultValue = email;
  if (!first_name?.length && !last_name?.length) return defaultValue || "";
  return [first_name || "", last_name || ""].join(" ");
}

function lng_variable(key: string, options = {}, _self: any = null) {
  if (typeof _self?.lng_global === "function")
    return _self?.lng_global(key, {
      ...options,
      file: "variables"
    });
  return "";
}

const userInitials = (user: User): string => {
  const { first_name, last_name } = user || {};
  const label = userLabel(user, user?.email);
  if (first_name?.length || last_name?.length)
    return label
      .split(" ")
      .map((name) => name[0])
      .join("").slice(0, 2);
  return (label || "")?.slice(0, 2) || "";
};

export { userLabel, userInitials, lng_variable };
